.msg_flex > div {
    background-color: #fdfdfd;
    border: 1px solid #bebebe;
    border-radius: 4px;
    padding: 0.4rem;
    margin-top: 0.4rem;
  }
  
  .msg_flex {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 100%;
    max-height: 250px;
    overflow-y: scroll;
  }
  .msg_flex::-webkit-scrollbar {
    display: none;
  }

  .messageBox {
    background: #F3F3F3;
    border-radius: 20px;
    padding: 5px 20px;
    color: white;
    display: inline-block;
    max-width: 80%;
  }
  
  .messageText {
    width: 100%;
    letter-spacing: 0;
    float: left;
    font-size: 1.1em;
    word-wrap: break-word;
  }
  
  .messageText img {
    vertical-align: middle;
  }
  
  .messageContainer {
    display: flex;
    justify-content: flex-end;
    padding: 0 5%;
    margin-top: 3px;
  }
  
  .sentText {
    display: flex;
    align-items: center;
    font-family: Helvetica;
    color: #828282;
    letter-spacing: 0.3px;
  }
  
  .pl-10 {
    padding-left: 10px;
  }
  
  .pr-10 {
    padding-right: 10px;
  }
  
  .justifyStart {
    justify-content: flex-start;
  }
  
  .justifyEnd {
    justify-content: flex-end;
  }
  
  .colorWhite {
    color: white;
  }
  
  .colorDark {
    color: #353535;
  }
  
  .backgroundBlue {
    background: #2979ff;
  }
  
  .backgroundLight {
    background: #c1afaf65;
  }
 
